<template>
    <div class="slideshow">
        <div class="content" @mouseenter="pause" @mouseleave="resume">
            <div class="move" :style="{ left: offeset + 'px' }">
                <Topic v-for="t in theShowList" :topic="t" :width="topicWidth" :key="t.id"></Topic>
            </div>
        </div>
        <!-- <div class="btn" @click="next">下一步</div>
        <div class="btn" @click="play">自动播放</div> -->
    </div>
</template>

<script setup>
// Carousel components
import { ref, defineProps } from "vue"

import Topic from './Topic.vue'

const topicMargin = 8 * 2 + 2    // Margins of the topic component
const topicWidth = 200           // Width of the topic component


const props = defineProps({
    topicList: {
        type: Array,           // The type of topic prop is an object
        required: true,         // The parent component must provide the prop
        default() {              // The default value of topic prop
            return [
                {
                    id: '',         // Topic number
                    name: '',       // Topic name
                    image: '',      // Topic picture
                    followCount: 0  // Number of followers of the topic
                }
            ]
        }
    }
})



// The list of rotating graphs shown is yes, and the topic list is copied three times
const theShowList = [...props.topicList, ...props.topicList, ...props.topicList]

// Whether to pause automatic scrolling
const paused = ref(false)

// Scroll distance, relative to the leftmost part of the display list
const offeset = ref(0)

if (!paused.value) {
    // Without pausing, automatic scrolling starts when the component loads
    play()
}

/**
 * Move a little distance
 */
function next() {
    const step = topicWidth + topicMargin
    offeset.value -= 1

    if (offeset.value <= -theShowList.length * step / 2) {
        // A width just equal to or greater than 50% of the display list resets the origin
        offeset.value = 0
    }
}

/**
 * Pause scrolling
 */
function pause() {
    paused.value = !paused.value
}

/**
 * Keep scrolling
 */
function resume() {
    paused.value = false
    play()
}

/**
* Start automatic scrolling
* (If paused, demand is suspended first, i.e. paused.value = false)
 */
function play() {
    if (paused.value == true) {
        // If pause is set, scroll will not be performed
        console.log('If pause is set, scroll is not performed')
        return
    }

    // Set the delay callback
    setTimeout(() => {
        next()
        play()  // Call the play function to scroll automatically
    }, 10)      // Time between each move, in milliseconds
}

</script>

<style scoped>
.slideshow {
    display: flex;
    width: 100%;
    height: 280px;
}

.slideshow>.content {
    display: flex;
    position: relative;

    overflow: hidden;
}

.slideshow>.content>.move {
    display: flex;

    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
}
</style>