<template>
    <div class="topic_list">
        <div class="list">
            <div class="topic_name">{{ props.name }}</div>
            <div class="topic" v-for="t in props.topicList" :key="t.id">
                <img class="image" alt="Vue logo" :src="t.image">
                <div class="text">{{ t.name }}</div>
            </div>
        </div>
    </div>
</template>

<script setup>
// Topic list component
import { defineProps } from "vue"

const props = defineProps({
    name: {                     // listing names
        type: String,
        required: true          // The parent component must provide the prop
    },
    topicList: {
        type: Array,           // The type of topic prop is an object
        required: true,         // The parent component must provide the prop
        default() {              // The default value of topic prop
            return [
                {
                    id: '',         // Topic number
                    name: '',       // name
                    image: '',      // picture
                    followCount: 0  // following
                }
            ]
        }
    }
})

console.log('Topic list', props)

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.topic_list {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    font-size: 0.9rem;

    flex: 1;
}

.topic_list:nth-child(1) {
    margin-right: 16px;
}

.topic_name {
    padding: 16px;
}

.topic_list>.list {
    flex: 1;
    display: flex;
    flex-direction: column;
    border: 1px solid #aaa;
    border-radius: 8px;
    overflow: auto;
}

.topic {
    border-top: 1px solid #aaa;
    display: flex;
    align-items: center;
}

.topic:hover {
    background: #cef1fd;
}

.topic:nth-child(1) {
    border-top: none;
}

.topic>.image {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;

    width: 28px;
    height: 28px;

    margin: 12px;
}

</style>